<template>   
    <v-container>     
        <v-card>       
            <v-container grid-list-md mb-12>
            <h2 class="text-md-center">Data Customer</h2>         
            <v-layout row wrap style="margin:10px">           
                <v-flex xs6 class="text-right">               
                    <v-text-field                 
                    v-model="keyword"                 
                    append-icon="mdi-search"                 
                    label="Search"                 
                    hide-details               
                    ></v-text-field>           
                </v-flex>
                <v-flex>          
                </v-flex>  
                <v-flex offset-xs4>           
                    <v-btn               
                    depressed               
                    dark               
                    rounded
                    href="http://localhost:8080/RESTSERVER_PAW/index.php/Report/customer"         
                    style="text-transform: none !important;"               
                    color = "green accent-3"              
                    >               
                    <v-icon size="18" class="mr-2">mdi-pencil-plus</v-icon>                   
                        Report               
                    </v-btn>           
                </v-flex>       
            </v-layout> 
 
            <v-data-table             
            :headers="headers"             
            :items="users"             
            :search="keyword"             
            :loading="load"           
            >             
            <template v-slot:body="{ items }">               
                <tbody>                 
                    <tr v-for="(item) in items" :key="item.id">                   
                        <td>{{ item.id }}</td>                   
                        <td>{{ item.name }}</td>                   
                        <td>{{ item.email}}</td>                   
                        <td>{{ item.birthdate }}</td>                   
                        <td>{{ item.gender }}</td>                   
                        <td>{{ item.address }}</td>                   
                        <td >                    
                            <v-btn 
                            icon                       
                            color="error"                       
                            light                       
                            @click="deleteData(item.id)"                     
                            >                       
                                <v-icon>mdi-delete</v-icon>                     
                            </v-btn>                   
                        </td>                 
                    </tr>               
                </tbody>             
            </template>           
        </v-data-table>       
    </v-container>     
    </v-card>          
                    <v-snackbar       
                    v-model="snackbar" 
                    :color="color"       
                    :multi-line="true"       
                    :timeout="3000"     
                    >       
                    {{ text }}       
                    <v-btn         
                     dark         
                     text         
                     @click="snackbar = false"       
                    >         
                        Close       
                    </v-btn>     
                </v-snackbar>   
            </v-container> 
        </template> 
<script> 
import { log } from 'util'
export default {    
    data () {       
        return {  
            menu2: false,      
            dialog: false,  
            pass: false,       
            keyword: '', 
            items: ['Male', 'Female'],        
            headers: [             
                {               
                    text: 'ID',               
                    value: 'id',             
                },             
                {               
                    text: 'Name',               
                    value: 'name'             
                },             
                {               
                    text: 'Email',               
                    value: 'email'             
                },             
                {               
                    text: 'Birthdate',               
                    value: 'birthdate'             
                },             
                {               
                    text: 'Gender',               
                    value: 'gender'             
                },             
                {               
                    text: 'Address',               
                    value: 'address'             
                },             
                {               
                    text: 'Aksi',               
                    value: null             
                },         
            ],         
            users: [],         
            snackbar: false,          
            color: null,         
            text: '',          
            load: false,    
            user : new FormData,         
            typeInput: 'new',        
            errors : '',         
            updatedId : '',       
        }     
    },     
    methods:{         
        getData(){
            // var config = {
            //     headers: {
            //         Authorization: 'Bearer ' + localStorage.getItem('token')
            //     }
            // }             
            var uri = this.$apiUrl + '/User/customer'             
            this.$http.get(uri).then(response =>{                 
                this.users=response.data.message             
            })               
        },         
        
        
        deleteData(deleteId){ //mengahapus data
            // var config = {
            //     headers: {
            //         Authorization: 'Bearer ' + localStorage.getItem('token')
            //     }
            // }              
            var uri = this.$apiUrl + '/User/' + deleteId; //data dihapus berdasarkan id 
            this.$http.delete(uri).then(response =>{                 
                this.snackbar = true;                 
                this.text = response.data.message;                 
                this.color = 'green'                 
                this.deleteDialog = false;                 
                this.getData();             
            }).catch(error =>{                 
                this.errors = error                 
                this.snackbar = true;                 
                this.text = 'Try Again';                 
                this.color = 'red'; 
            })         
        },
    },     
    mounted(){         
        this.getData();     
        }, 
    } 
</script> 
  